<template>
  <div>
    <div class="read">
      <p class="read-title">未读消息</p>
      <div v-if="hasNotMsg.length" class="read-list">
        <div @click="goMotif(item.topic.id)" class="read-cell" v-for="item in hasNotMsg">
          <!--<div class="read-mask"></div>-->
          <div class="markdown-body" v-html="item.reply.content"></div>
          <p>{{item.author.loginname}}在 <span class="blue">{{item.topic.title}}</span> 中回复了你</p>
          <div class="read-time">{{item.create_at|time-filter}}</div>
        </div>
      </div>
      <div v-else class="text-center l-h">无未读消息</div>
    </div>
    <div class="read">
      <p class="read-title">已读消息</p>
      <div v-if="hasMsg.length" class="read-list">
        <div class="read-cell" v-for="item in hasMsg">
          <div class="read-mask"></div>
          <div class="markdown-body" v-html="item.reply.content"></div>
          <p>{{item.author.loginname}}在 <span class="blue">{{item.topic.title}}</span> 中回复了你</p>
          <div class="read-time">{{item.create_at|time-filter}}</div>
        </div>
      </div>
      <div v-else class="text-center l-h">无已读消息</div>
    </div>
  </div>
</template>
<script>
  import dataUrl from '../api/DataUrl'
  import storage from '../api/Storage'
  import '../filters/filter'
  export default {
    data() {
      return {
        hasNotMsg:[{
          author:{},
          reply:{},
          topic:{}
        }],
        hasMsg:[{
          author:{},
          reply:{},
          topic:{}
        }]
      }
    },
    methods:{
      goMotif(id) {
        this.$router.push('/motif/'+id);
      }
    },
    mounted(){
      let vm = this;
      let user = storage.user();
      if (user && user.token) {

        dataUrl.getMessages(user.token).then((res)=>{
          vm.hasNotMsg = res.body.data.hasnot_read_messages;
          vm.hasMsg = res.body.data.has_read_messages

          dataUrl.postMsgMark_all(user.token).then((r)=>{
          })

        })

      }else {
        alert('token错误,请重新登录')
      }
    }
  }
</script>

<style>
  .hasnot-read {

  }

  .read-title{
    padding: 4px 10px;
    background-color: #eee;
  }

  .read-cell {
    padding: 0 10px;
    border-bottom: 1px solid #e8e8e8;
    position: relative;
  }

  .read-list {

  }

  .blue {
    color: dodgerblue;
  }

  .read-time {
    height: 25px;
    line-height: 25px;
    text-align: right;
  }
  .l-h{
    line-height: 30px;
  }

  .read-mask {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
  }
</style>



